.scoreboard {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-radius: 6px;
  border: 2px solid #ff9a00;
  width: 80%;
  font-size: 1.5rem;
  margin: 0.5rem auto 2rem;
  box-shadow: 0px 0px 5px rgba(255, 154, 0, 0.7),
    0px 0px 15px rgba(255, 154, 0, 0.7);
}

.x-container {
  color: #ff2e63;
  text-shadow: 0px 0px 5px rgba(255, 0, 0, 0.9),
    0px 0px 15px rgba(255, 0, 0, 0.9);
  border: 2px solid #801731;
  padding: 0.5rem 1rem;
  box-shadow: 0px 0px 5px rgba(255, 0, 0, 0.9),
    0px 0px 15px rgba(255, 0, 0, 0.9);
}

.o-container {
  color: #08d9d6;
  text-shadow: 0px 0px 5px rgba(8, 217, 214, 0.9),
    0px 0px 15px rgba(8, 217, 214, 0.9);
  border: 2px solid #08d9d6;
  box-shadow: 0px 0px 5px rgba(8, 217, 214, 0.9),
    0px 0px 15px rgba(8, 217, 214, 0.9);
  padding: 0.5rem 1rem;
}

.title {
  font-size: 1.75rem;
  color: #ff9a00;
  text-shadow: 0px 0px 5px rgba(255, 154, 0, 0.7),
    0px 0px 15px rgba(255, 154, 0, 0.7);
  animation-name: pulse;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  padding: 0.5rem;
  margin: 0;
}
